import React from 'react'
import ChuckNorrisAPI from '../services/chuckNorris'
import useAsync from './../hooks/useAsync'
import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import CategoryListItem from './CategoryListItem'
import './../../style/CategoryList.css'
import PropTypes from 'prop-types'
import {bindActionCreators} from 'redux';
import {setCategories} from "../redux/actions";
import {connect} from 'react-redux';

const CreateGrid = ({categories}) => {
    return (
        <Grid container spacing={8} style={{padding: 12}}>
            {
                categories.map(value => (
                    <Grid item xs={12} sm={6} lg={3} xl={3} md={3} key={value}>
                        <CategoryListItem category={value}/>
                    </Grid>
                ))
            }
        </Grid>
    )
}

const CategoryList  = (props) => {
    const { error, loading } = useAsync(async () => {
        const response = await ChuckNorrisAPI.categories()
        setCategories(response.responseData)
        return response
    })
    const { categories, setCategories } = props
    if (error) {
        return (
            <p>
                {JSON.stringify(error)}
            </p>
        )
    }
    if (loading) {
        return (
            <div>
                <CircularProgress/>
            </div>
        )
    } else {
        return (
            <div className={"Grid-div-root"}>
                <CreateGrid categories={categories} />
            </div>
        )
    }
}

CreateGrid.propTypes = {
    categories: PropTypes.arrayOf(
        PropTypes.string.isRequired
    ).isRequired
}

const mapStateToProps = store => ({
    categories: store.categoriesState.value
})

const mapDispatchToProps = dispatch =>
    bindActionCreators({setCategories}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(CategoryList)