import * as types from './../actions/types'

const initialState = {
    byCategory: {},
    values: []
}

export const jokesReducer = (state = initialState, action) => {
    switch(action.type) {
        case types.SET_JOKE:
            const joke = action.value
            const { byCategory, values } = state
            let updatedByCategory = {}
            if(joke.category){
                joke.category.map(value => {
                    const updatedArray = []
                    updatedArray.push(...(byCategory[value] || []), joke)
                    updatedByCategory[value] = updatedArray
                    return value
                })
            } else {
                const updatedArray = []
                updatedArray.push(...(byCategory['explicit'] || []), joke)
                updatedByCategory['explicit'] = updatedArray
            }
            const newValues = []
            newValues.push(...values, joke)
            return {
                byCategory: {
                    ...byCategory,
                    ...updatedByCategory
                },
                values: newValues
            }
        default:
            return state
    }
}