async function processResponse(response) {
    if(response.ok) {
        return { responseData: await response.json(), responseError: null }
    } else {
        return { responseData: null, responseError: new Error(response.statusText)}
    }
}

class ChuckNorrisAPI {
    _address = 'https://api.chucknorris.io'
    categories = async () => {
        const response = await fetch(`${this._address}/jokes/categories`)
        return processResponse(response)
    }
    randomJokeFromCategory = async (category) => {
        const response = await fetch(`${this._address}/jokes/random?category=${category}`)
        return processResponse(response)
    }
}

export default new ChuckNorrisAPI()