import React, {useState} from 'react'
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import Badge from '@material-ui/core/Badge';
import PropTypes from "prop-types";
import Typography from "@material-ui/core/es/Typography/Typography";

const CategoriesMenu = ({categories, jokes}) => {
    const [anchorEl, setAnchorEl] = useState(null)
    return (
        <div>
            <Button
                variant="contained"
                aria-owns={anchorEl ? 'simple-menu' : undefined}
                aria-haspopup="true"
                onClick={event => setAnchorEl(event.currentTarget)}
            >
                Categories
            </Button>
            <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={() => setAnchorEl(null)}
            >
                {
                    categories.map(category => {
                        const jokesByCategory = jokes.byCategory[category]
                        return (
                            <MenuItem key={category} onClick={() => setAnchorEl(null)}>
                                <Badge color="primary" badgeContent={jokesByCategory ? jokesByCategory.length : 0}>
                                    <Typography>
                                        {category}
                                    </Typography>
                                </Badge>
                            </MenuItem>
                        )
                    })
                }
            </Menu>
        </div>
    )
}

CategoriesMenu.propTypes = {
    categories: PropTypes.array.isRequired,
    jokes: PropTypes.object.isRequired
}

export default CategoriesMenu